define('ember-cli-idcos-docs/router', ['exports', 'ember', 'ember-cli-idcos-docs/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route('home');
    this.route('newbee');
    this.route('document', function () {
      this.route('button');
      this.route('select');
      this.route('searchableSelect');
      this.route('table');
      this.route('modal');
      this.route('tree');
      this.route('form');
      this.route('form-validator');
      this.route('newbee');
      this.route('introduce');
      this.route('icon');
      this.route('layout');
      this.route('checkbox');
      this.route('radio');
      this.route('menu');
      this.route('cascader');
      this.route('upload');
      this.route('switch');
      this.route('split');
    });
  });

  exports['default'] = Router;

});